html {
    font-family: sans-serif;
}
a {
    color: rgb(3, 3, 147);
    text-decoration: none;
}
section p {
    text-align: center;
    background-color: rgb(145, 207, 2);
    margin-bottom: 10px;
    padding: 10px;
}
button, #btn {
    text-align: center; 
    padding:5px 18px; 
    font-size: 3rem; 
    border-radius: 3rem;
    background-color: rgb(104, 205, 9);
    border: none;
    color: white;
    justify-self: flex-start;
    max-height: 6rem;
    max-width: 20rem;
    cursor: pointer;
}
#btn {
    position: fixed;
    top: auto;
    bottom: 10px;
    right: 10px;
    z-index: 3;
    transition: 2s;
}
.btn-click {
    animation-duration: 2s;
    animation-name: down ;
}
form {
    display: flex;
    flex-direction: column;

}
form div label{
    display: inline-block;
    width: 9rem;
    text-align: right;
    color: rgb(4, 75, 23);
    font-size: larger;
}
form div input {
    width: 60%;
    border-radius: 2rem;
    margin-block-end: 1rem;
    border: 3px solid rgb(73, 218, 10);
    outline-color: rgb(39, 59, 2);
    font-size: 1.5rem;
    color: green;
    
}

#img-otra {
    display: flex; 
    flex-wrap: wrap;
    justify-content: center;
}
#img-otra figure, 
#img-otra figure img {
    max-width: 640px;
    object-fit: cover;
    border-radius: 2rem;
}

#img-otra figure {
    animation-duration: 4s;
    animation-name: fadetr;
}
@keyframes fadetr {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
@keyframes down {
    0% {
      transform: translateY(30px);
      opacity: .8;
    }
    25%{
        transform: translateY(0);
        opacity: .9;
    }
    50%{
        transform: translateY(30px);
        opacity: .9;
    }
    100% {
        transform: translateY(0);
        opacity: .8;
    }
  }
@media (max-width: 640px){
    #img-otra{
        width: 100%;
    }
    figure,
    img {
        width: 100%;
        height: auto;
    }
    form div, form {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    form div label {
        width: fit-content;
    }
    form div input {
        width: 80%;
    }
}